import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core'; 
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { HeaderAccountInfoModule } from '../header-account-info/header-account-info.module';
import { SearchVoucherComponent } from './search-voucher/search-voucher.component';
import { ComponentsModule } from '../components/components.module';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { SalesOrderPaymentCardComponent } from './sales-order-payment-card.component';
import { CreateBillComponent } from './create-bill/create-bill.component';
const routes: Routes = [
  {
    path: '',
    component: SalesOrderPaymentCardComponent,
  }, 
  {
    path: 'add',
    component: CreateBillComponent,
  }
]


@NgModule({
  declarations: [SalesOrderPaymentCardComponent, CreateBillComponent, SearchVoucherComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule, 
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    IonicModule,
    HeaderAccountInfoModule,
    ComponentsModule,
    ScrollingModule
  ]
})
export class SalesOrderPaymentCardModule { }
